*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

html{
  height: 100%;
}
body {
  background-color: #6fb8bd;
  height: 100%;
  margin: 0;
  padding: 40px 0;
  font-family: "Roboto";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  @media (max-width: 640px) {
    padding: 0;
    background-color: white;
  }
}
.active{
  color: rgb(0, 0, 0);
}
.btn{
  font-size: 18px;
  padding: 10px 20px;
  border-radius: 10px;
  border: none;
  background: #6fb8bd;
  color: white;
  cursor: pointer;
}

.container{
  padding: 0 40px;
  @media (max-width: 535px) {
    padding: 0 20px;
  }
}
.title_page{
  margin-top: 30px;
  margin-bottom: 30px;
  @media (max-width: 530px) {
    margin: 15px 0;
  }
}
.wrong{
  font-size: 14px;
  color: red;
  margin-bottom: 5px;
}

.skeleton{
  border: 2px solid #f3f3f3;
  border-radius: 15px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.wrapper{
  background-color: white;
  padding-bottom: 40px;
  position: relative;
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;
  border-radius: 30px;
  min-height:calc(100vh - 80px);
  margin-bottom: 40px;
  @media (max-width: 640px) {
    border-radius: 0;
  }
}

a{
  color: inherit;
  text-decoration: none;
}
ul{
  list-style-type: none;
}
/*HEADER===============================*/

.header{
  width: 100%;
  padding: 0 40px;
  font-size: 18px;
  position: relative;
  @media (max-width: 535px) {
    padding: 0 20px;
  }
  .header_container{
    display: flex;
    justify-content: space-between;
    height: 80px;
    align-items: center;
  }
}

.logo{
  font-size: 40px;
  font-weight: bold;
}


.menu_list{
  display: flex;
  gap: 20px;
  align-items: center;
  order: 1;
}
.profile{
  display: flex;
  align-items: center;
  gap: 10px;
  order: 2;
}
.burger{
  display: none;
}

@media (max-width: 900px) {
  .burger{
    position: relative;
    display: block;
    width: 40px;
    height: 25px;
    order: 1;
  }
  .burger:before,.burger:after{
    content: '';
    display: block;
    background: #6fb8bd;
    width: 100%;
    height: 3px;
    left: 0;
    position: absolute;
    transition: all 0.3s ease-out;
  }
  .burger:after{
    bottom: 0;
  }
  .burger span {
    display: block;
    background: #6fb8bd;
    width: 100%;
    height: 3px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .burger.active_burger:before{
    transform: rotate(-45deg) ;
    top:50%;
  }
  .burger.active_burger:after{
    transform: rotate(45deg);
    top: 50%;
  }
  .burger.active_burger span{
    transform: scale(0);
  }

  .logo{
    order: 2;
  }
  .header_menu{
    position: fixed;
    top: 120px;
    background: rgba(111, 184, 189, 0.97);
    height: 100%;
    width: 300px;
    left: -100%;
    z-index: 3;
    transition: all 0.3s ease-out;
  }
  .active_menu{
    left: 0;
  }
  .menu_list{
    flex-direction: column;
    margin-top: 35px;
    align-items: flex-start;
    margin-left: 40px;
  }
  .menu_item{
    color: white;
    font-size: 24px;
  }
}
@media (max-width: 640px) {
  .header_menu{
    top: 80px;
  }
}

@media (max-width: 565px) {
  .header_menu{
    width: 100%;
  }
}

@media (max-width: 465px) {
  .logo{
    font-size: 35px;
  }
  .burger{
    width: 35px;
    height: 22px;
  }
}
.btn_header{
  &:hover{ 
    background: #4e8185;
  }
  @media (max-width: 415px) {
    padding: 8px 15px;
  }
}

.menu_item.popup{
  position: relative;
  &:hover .popup_menu{
    max-height: 500px;
  }
  a{
    display: block;
    line-height: 30px;
  }
  .popup_menu{
    transition: max-height 0.3s ease;
    position: absolute;
    max-height: 0;
    z-index: 10;
    left: -15px;
    background: #ffffff;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.09);
    border-radius: 10px;
    overflow: hidden;
    width: auto;
    color: black;
    .pop_menu_item{
      @media (max-width: 900px) {
        font-size: 20px;
        color: #6fb8bd;
      }
      a{
        display: inline-block;
        padding: 10px 15px;
        width: 100%;
        @media (max-width: 900px) {
          padding: 6px 10px;
        }
        &:hover{
          background: #e0e0e0;
        }
      }

    }
  }
}

.line{
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 2px;
  background: #6fb8bd;
}

//SORT================================
.sort_component{
  margin-top: 40px;
  margin-bottom: 40px;
  .sort_page_body{
    position: relative;
    width: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 920px) {
      display: block;
    }
  }
  .search{
    .input_search{
      font-size: 17px;
      padding: 5px 10px;
      padding-right: 30px;
      border: 2px solid #c9c9c9;
      border-radius: 5px;
      outline: none;
      &:focus{
        border: 2px solid black;
      }
    }
  }
  .sort_popup{
     position: absolute;
     z-index: 10;
     right: 0;
     top: 40px;
     background: #ffffff;
     box-shadow: 0 5px 15px rgba(0, 0, 0, 0.09);
     border-radius: 10px;
     overflow: hidden;
     width: auto;
    color: #6fb8bd;
    @media (max-width: 535px) {
      right: -20px;
    }
    ul {
      overflow: hidden;
      li {
        padding: 12px 14px;
        white-space: nowrap;
        cursor: pointer;
        color: black;
        font-size: 17px;
        &.active_sort_pop,
        &:hover {
          color: #6fb8bd;
          background: rgba(195, 245, 252, 0.3);
        }
      }
    }
   }
  .sort{
    position: relative;
    @media (max-width: 920px) {
      margin-top: 25px;
      width: auto;
    }
    display: inline-flex;
    align-items: center;
    div{
      font-size: 18px;
      margin-right: 10px;
    }
    span{
      color: #6fb8bd;
      cursor: pointer;
      border-bottom: 1px dashed #6fb8bd;
      font-size: 17px;
    }

  }
}

//RECIPES=====================
.recipe_blocks{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 40px;
  margin-bottom: 20px;
}
.recipe_block_wrapper{
  height: 320px;
  margin-bottom: 30px;
  width: 260px;
}
.recipe_block_body{
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.09);
  background: rgba(235, 250, 250, 0.8);
  position: relative;
  width: 260px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 15px;
  max-width: 260px;
  transition: all 0.2s ease;
  height: 360px;
  overflow: hidden;
  &:hover{
    transform: scale(1.05);
    transition: all 0.2s;
    min-height: 360px;
    height:auto;
    z-index: 1;
  }
  &:hover .product_wrapper{
    height: auto;
  }
  &:hover .products{
    display: block;
  }
  .img_bookmark{
    position: absolute;
    top: 10px;
    left:10px;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid #afafaf;
    z-index: 2;
    background: rgba(235, 250, 250, 0.8);

  }
  .img_bookmark_active{
    background: rgba(245, 120, 120, 0.71);
  }
}
.block_img{
  min-width: 100%;
  min-height: 150px;
  overflow: hidden;
  border-radius: 10px;
  position: relative;
  margin-bottom: 15px;

  img{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    object-fit: contain;
    object-position: center;
    height: 100%;
    display: block;
  }
}
.recipe_name{
  font-size: 19px;
  margin-bottom: 8px;
  text-align: center;
  flex: 1 1 auto;
}
.recipe_inf{
  width: 100%;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  justify-content: space-between;
  margin-bottom: 5px;
  .time_cook{
    margin-bottom: 4px;
  }
}
.product_wrapper{
  overflow: hidden;
  height: auto;
}
.products {
  height:auto;
  text-align: left;
  -webkit-line-clamp: 2;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}


//Cook=====================
.meal_list{
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-bottom: 30px;

  .meal_type{
    border-radius: 30px;
    font-size: 20px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.09);
    cursor: pointer;
    padding: 12px 35px;
    background: white;
    
  }
  .checked{
    background: #6fb8bd;
    color: white;
  }
}
.cook_page{
  .random_cook{
    margin-bottom: 40px;
  }
  .random_btn{
    font-size: 18px;
    padding: 10px 20px;
    border-radius: 10px;
    font-weight: 600;
    color: #6fb8bd;
    background: white;
    cursor: pointer;
    border: 2px solid #6fb8bd;
    transition: all 0.2s ease;
    &:hover{
      background: #6fb8bd;
      color: white;
    }
  }
}



.products_has, .product_need{
  font-size: 20px;
  .add_product_btn{
    margin: 8px 0 20px 20px;
  }
  div{
    margin-bottom: 15px;
  }
  span{
    font-size: 14px;
    color: #60989b;
    @media (max-width: 500px) {
      display: block;
      margin-top: 8px;
    }
  }
  .product_has_list{
    padding: 15px 15px;
    font-size: 18px;
    width: 350px;
    height: 180px;
    overflow: auto;
    resize: none;
    margin-bottom: 20px;
    border-radius: 15px;
    border: 2px solid #c5c3c3;
    @media (max-width: 430px) {
      width: 100%;
    }
  }
}
.dob_sum{
  font-size: 20px;
  div{
    margin-bottom: 15px;
  }
  .sum{
    width: 100px;
    padding: 8px 15px;
    font-size: 18px;
    margin-bottom: 20px;
    margin-right: 5px;
    border-radius: 10px;
    border: 2px solid #c5c3c3;
  }
}
.overlay_login{
  width:100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  background: rgba(157, 174, 176, 0.91);
}
.overlayEnter{
  opacity: 0;
}
.overlayEnterActive{
  transition: opacity .3s ease-out;
  opacity: 1;
}
.overlayExit{
  opacity: 1;
}
.overlayExitActive{
  transition: opacity .3s ease-out;
  opacity: 0;
}

.popupEnter{
  transform: translateY(-75vh);
}
.popupEnterActive{
  transition: transform 0.3s ease-out;
  transform: translateY(0);
}
.popupExit{
   transform: translateY(0);
}
.popupExitActive{
  transition: transform 0.3s ease-out;
  transform: translateY(-75vh);
}


.login_block_wrapper {
  width:100vw;
  height: 100vh;
  position: fixed;
  padding: 0 20px;
  display: flex;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
  z-index: 3;
  .login_block {
    width: 400px;
    height: auto;
    background: white;
    position: relative;
    padding: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.09);
    border-radius: 25px;
    font-size: 18px;
    .cross{
      position: absolute;
      width: 35px;
      height: 35px;
      border: 3px solid #6fb8bd;
      border-radius: 50%;
      top: 22px;
      right:  22px;
      cursor: pointer;
      span{
        display: block;
        width: 80%;
        height: 2px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%) rotate(45deg);
        background: #6fb8bd;
      }
      .line{
        transform: translate(-50%,-50%) rotate(-45deg);
      }
    }

    input {
      display: block;
      margin-top: 5px;
      margin-bottom: 10px;
      padding: 4px 8px;
      font-size: 16px;
      width: 80%;
    }
    .buttons{
      margin-top: 20px;
      button{
        font-size: 18px;
        padding: 8px 18px;
        border-radius: 8px;
        border:none;
        background: #92a0a1;
        color: white;
        cursor: pointer;
      }
      .enter{
        margin-right: 10px;
        background: #6fb8bd;
      }
    }
  }
}


.active_login{
  display: flex;
}


//Profile========================
.page_top{
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 510px) {
    display: block;
  }

  .add_recipe{
    padding: 10px 25px;
    background: #6fb8bd;
    border: none;
    border-radius: 10px;
    font-size: 17px;
    cursor: pointer;
    color: white;
    @media (max-width: 510px) {
      margin-bottom: 25px;
    }
  }
}

.list_user_recipes{
  margin-bottom: 30px;
}

.profile_content{
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  margin-bottom: 70px;
  position: relative;
  align-items: flex-start;
  @media (max-width: 1000px) {
    justify-content: center;
  }
  @media (max-width: 560px) {
    gap: 20px;
  }
  .column_img{
    flex-basis: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%;
    position: relative;
    border: none;
    @media (max-width: 510px) {
      text-align: center;
    }
    .profile_img{
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
      object-position: center;
    }
  }
  .column_content{
    .profile_name{
      font-size: 40px;
      color: #6fb8bd;
      margin-bottom: 15px;
      @media (max-width: 536px) {
        text-align: center;
      }
    }
    .profile_inf{
      font-size: 20px;
      margin-bottom: 10px;
    }
    .btn_profile{
      padding: 10px 20px;
      border-radius: 10px;
      border: none;
      font-size: 20px;
      color: white;
      background: #6fb8bd;
      margin-top: 10px;
      cursor: pointer;
    }
  }
}

.column_render{
  display: flex;
  justify-content: right;
  flex: 1 1 auto;
  @media (max-width: 1000px) {
    justify-content: center;
  }
  .render_wrapper{
    padding: 25px;
    border: 3px solid #6fb8bd;
    border-radius: 15px;
    @media (max-width: 470px) {
      max-width: 335px;
    }
  }
}
.render{
  display: flex;
  flex-direction: column;
  font-size: 19px;
  .render_block{
    display: flex;
    justify-content: space-between;
    gap: 20px;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
    @media (max-width: 470px) {
      gap: 15px;
    }
    &:last-child{
      margin-bottom: 0;
    }
    .render_input{
      font-size: 18px;
      padding: 4px 8px;
      border-radius: 5px;
      border:1px solid #c5c3c3;
      outline: none;
      @media (max-width: 470px) {
        width: 150px;
      }
    }
  }
  .err_inf{
    font-size: 14px;
    color: red;
    margin-bottom: 20px;
    margin-top: -20px;
  }
  .dop_inf{
    font-size: 22px;
    margin-bottom: 15px;
    color: #63a3a8;
  }
  button{
    font-size: 19px;
    display: inline-block;
    border-radius: 5px;
    background: white;
    color: #6fb8bd;
    border: 2px solid #6fb8bd;
    padding: 5px 10px;
    cursor: pointer;
    &:hover{
      background:#6fb8bd;
      color: white;
      transition: all 0.2s ease;
    }
  }
}
.reg_wrapper{
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
//Add Recipe======================
.add_recipe_block_wrapper{
  display: flex;
  justify-content: left;
}
.add_recipe_block{
  display: flex;
  flex-direction: column;
  .add_recipe_btn{
    padding: 10px;
    background: white;
    color: #6fb8bd;
    border: 2px solid #6fb8bd;
    transition: all 0.2s ease;
    &:hover{
      background: #6fb8bd;
      color: white;
    }
  }
}
.add_recipe{
  display: inline;
  padding: 5px 20px;
}
.add_recipe_block{
  display: flex;
  flex-direction: column;
  @media (max-width: 570px) {
    align-items: center;
  }
}
.add_input{
  font-size: 20px;
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 570px) {
    flex-direction: column;
    align-items: center;
    width: 320px;
  }
  input{
    padding: 4px 8px;
    font-size: 18px;
    width: 270px;
    @media (max-width: 570px) {
      margin-top: 15px;
      width: 100%;
    }
  }
}
.empty_list{
  font-size: 22px;
}

//AddProduct=================
.product_to_add{
  display: flex;
  justify-content: left;
  gap: 10px;
  align-items: center;
  .product_number{
    font-size: 18px;
    line-height: 30px;
  }
  .product_add_list{
    margin-bottom: 0;
    flex: 1 1 auto;
    @media (max-width: 620px) {
      flex: 0 0;
    }
  }
  .remove_product{
    display: inline-block;
    padding: 13px;
    background: white;
    border: 2px solid red;
    margin-bottom: 0;
    position: relative;
    border-radius: 6px;
    cursor: pointer;
    @media (max-width: 620px) {
      margin-left: 20px;
      }
    &:after{
      content: '';
      display: block;
      width: 70%;
      height: 3px;
      background: red;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
  }
}

.product_add_list{
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 0;
  gap: 15px;
  align-items: center;
  .product_name,.product_col,.product_measure{
    padding: 4px 8px;
    font-size: 17px;
    outline: none;
    border: 2px solid #6fb8bd;
    border-radius: 5px;
  }
}
.rendering{
  margin-left: 20px;
  margin-right: 40px;
  margin-top: 25px;
}